import React from 'react'
import { Navbar, Nav, Container, NavDropdown,  } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import {useDispatch,useSelector} from "react-redux"
import { logout } from '../actions/userActions';
import { Link, Route } from 'react-router-dom';
import SearchBox from './SearchBox';

const Header = () => {
  const dispatch=useDispatch()
    const userLogin = useSelector((state) => state.userLogin);
    const { userInfo } = userLogin;
    const logoutHandler=()=>{
      console.log("logout")
      dispatch(logout())
    }
    return (
      <header>
        <Navbar className="bg" variant="dark" expand="lg" collapseOnSelect>
          <Container>
            <LinkContainer to="/">
              <Navbar.Brand href="/" className="brand">
                BESTBUY
              </Navbar.Brand>
            </LinkContainer>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Route render={({ history }) => <SearchBox history={history} />}/>
              
                
              <Nav className="ml-auto">
                <LinkContainer to="/">
                  <Nav.Link className="nav-link">
                    <i className="fa fa-home mr-1" aria-hidden="true"></i>HOME
                  </Nav.Link>
                </LinkContainer>
                <LinkContainer to="/cart">
                  <Nav.Link className="nav-link">
                    <i className="fas fa-shopping-cart mr-1"></i>CART
                  </Nav.Link>
                </LinkContainer>
                {userInfo ? (
                  <NavDropdown title={userInfo.name} id="username" style={{textTransform:"uppercase"}}>
                    <LinkContainer to="/profile">
                      <NavDropdown.Item>Profile</NavDropdown.Item>
                    </LinkContainer>
                    <NavDropdown.Item onClick={logoutHandler}>
                      Logout
                    </NavDropdown.Item>
                  </NavDropdown>
                ) : (
                  <LinkContainer to="/login">
                    <Nav.Link className="nav-link">
                      <i className="fas fa-user mr-1"></i>SIGN IN
                    </Nav.Link>
                  </LinkContainer>
                )}

                {userInfo && userInfo.isAdmin && (
                  <NavDropdown title="Admin" id="adminmenu">
                    <LinkContainer to="/admin/userlist">
                      <NavDropdown.Item>Users</NavDropdown.Item>
                    </LinkContainer>
                    <LinkContainer to="/admin/productlist">
                      <NavDropdown.Item>Products</NavDropdown.Item>
                    </LinkContainer>
                    <LinkContainer to="/admin/orderlist">
                      <NavDropdown.Item>Orders</NavDropdown.Item>
                    </LinkContainer>
                  </NavDropdown>
                )}
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
      </header>
    );
}

export default Header
